___CSS_LOADER_EXPORT___.push([module.id, ".wrapper-page{background-color:#fafafa;height:100%;width:100%}.page-heading{display:flex;justify-content:space-between}.page-details{border-bottom:1px solid rgba(0,0,0,.1);border-right:1px solid rgba(0,0,0,.1);width:256px}.page-search{align-items:center;border-bottom:1px solid rgba(0,0,0,.1);display:flex;width:100%}.content-wrapper{display:flex;height:100%;position:relative;width:100%}.content{margin-left:256px;transition:all .3s ease;width:100%}.filter{border:.5px solid #cdcdcd;border-radius:15px;cursor:pointer}.filter:hover{color:red}.content.full{margin-left:0;width:100%}@media (max-width:960px){.content{margin-left:0}.page-details{width:200px}.page-search{width:calc(100% - 200px)}}@media (max-width:740px){.page-details{width:0}.page-search{width:100%}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Explorador/index.vue"],"names":[],"mappings":"AAkkBA,cAGA,wBAAA,CADA,WAAA,CADA,UAGA,CAEA,cACA,YAAA,CACA,6BACA,CACA,cAEA,sCAAA,CACA,qCAAA,CAFA,WAGA,CACA,aAEA,kBAAA,CAEA,sCAAA,CAHA,YAAA,CAEA,UAEA,CAEA,iBAEA,YAAA,CAEA,WAAA,CAHA,iBAAA,CAEA,UAEA,CAEA,SAEA,iBAAA,CACA,uBAAA,CAFA,UAGA,CACA,QAEA,yBAAA,CADA,kBAAA,CAEA,cACA,CACA,cACA,SACA,CAEA,cAEA,aAAA,CADA,UAEA,CAEA,yBACA,SACA,aACA,CAEA,cACA,WACA,CACA,aACA,wBACA,CACA,CAEA,yBACA,cACA,OACA,CACA,aACA,UACA,CACA","sourcesContent":["<template>\r\n    <div class=\"wrapper-page\">\r\n        <div class=\"content-wrapper\">\r\n            <v-navigation-drawer\r\n                absolute\r\n                v-model=\"drawer\"\r\n                color=\"grey lighten-5\"\r\n            >\r\n\r\n                <v-container>\r\n\r\n                    <div class=\"mt-5\">\r\n                        <h4 class=\"pb-3 grey--text text--darken-2\">\r\n                            Filtros\r\n                        </h4>\r\n                        <v-menu\r\n                            ref=\"menux\"\r\n                            v-model=\"menux\"\r\n                            :close-o